<template>
  <div>
    <div class="sort">
        <slot name="sort" ></slot>
    </div>
  <Table border :loading="loading" :columns="columns1" :data="data1" ref="table" @on-select="onSelect"></Table>
    <br>
    <Button type="primary" size="large" @click="exportData(1)">
      <Icon type="ios-download-outline">
      </Icon> 导出原始数据</Button>
    <Button type="primary" size="large" @click="exportData(2)"><Icon type="ios-download-outline">
    </Icon> 导出排序和过滤后的数据</Button>
    <Button type="primary" size="large" @click="exportData(3)"><Icon type="ios-download-outline">
    </Icon> 导出选中的行数据</Button>
    <br />
    <Page class="page" :total="totalSize" show-sizer show-elevator show-total size="small"
          :page-size="10" :page-size-opts="pageSizeOpts" :placement="placement"
          :current="current"
          @on-page-size-change="pageSizeChange"
          @on-change="onChange"></Page>
  </div>
</template>
<script>
  export default {
    props:{
      current:{
        type:Number
      },
      data:{                    //列数据
        type:Array,
      },
      columns:{                //行数据
        type:Array,
      },
      totalSize:{            //数据的总条数
        type:Number
      },
      pageSizeOpts:{          //可以选择的每页显示的条数
        type:Array,
        default:[10,30,50,100]
      },
      placement:{             //条数切换弹窗的展开方向
        type:String,
        default:"bottom"
      }
    },
    data () {
      return {
        loading: true,         //loading
        columns1: [],          //行数据
        data1: [],            //列数据
        line:[],             //选择的行
        pageSize:10
      }
    },
    mounted(){
      this.data1 = this.data,
        this.columns1 = this.columns
    },
    methods:{
      exportData (type) {
        if (type === 1) {
          this.$refs.table.exportCsv({
            filename: '原始数据',
          });
        }else if (type === 2) {
          this.$refs.table.exportCsv({
            filename: '排序和过滤后的数据',
            original: false
          });
        }else if (type === 3) {
          this.$refs.table.exportCsv({
            filename: '导出选中的行数据',
            columns: this.columns1,
            data: this.line
          });
        }
      },
      onSelect(selection,row){       //返回选择的行
        this.line = selection
      },
      pageSizeChange(pageSize){      //返回当前选择的是几条/页
        this.$emit('changePage',pageSize)
      },
      onChange(index){                //页码改变的回调，返回改变后的页码
        this.$emit('changeTable',index)
      }
    },
    watch:{
      //监听数据变化 数据加载完后初始化
      data(){
        setTimeout(()=>{
          this.data1 = this.data,
            this.loading = false
        },20)
      },
    }
  }
</script>
<style>
  .page{
    margin-top: 20px;
    margin-bottom: 10px;
  }
  .sort{
    margin-bottom: 10px;
    text-align: left;
  }
</style>



