<template>
    <div class="hietory-table">
      <base-table :data="history" :columns="columns"
                  :totalSize="totalSize"
                  :pageSizeOpts="pageSizeOpts"
                  :placement="placement"
                  :current="index"
                  @changePage="changePage"
                   @changeTable="changeTable">
        <Select v-model="model2" size="small" style="width:100px;margin-right: 10px" slot="sort" placeholder="排序" @on-change="sort">
        <Option v-for="item in List" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </Select>

      </base-table>
    </div>
</template>

<script>
import BaseTable from "../base/base-table.vue"
import {getHistoryUrl,getSortUrl} from  '../common/js/url'

  export default {
  data(){
    return{
      history:[],
      columns:[{
        type: 'selection',
        width: 60,
        align: 'center'
      },
        {
          title: 'qqId',
          key: 'qqid',
          sortable: true
        },
        {
          title: 'myId',
          key: 'myid',
          sortable: true
        },
        {
          title: 'Jitter',
          key: 'jitter',
          sortable: true,
          filters: [
            {
              label: '大于0.15%',
              value: 1
            },
            {
              label: '小于0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.jitter>'0.15%';
            } else if (value === 2) {
              return row.jitter < '0.5%';
            }
          }
        },
        {
          title: 'Shimmer',
          key: 'shimmer',
          sortable: true,
          filters: [
            {
              label: '大于0.15%',
              value: 1
            },
            {
              label: '小于0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.shimmer>'0.15%';
            } else if (value === 2) {
              return row.shimmer < '0.5%';
            }
          }
        },
        {
          title: 'HNR',
          key: 'hnr',
          sortable: true,
          filters: [
            {
              label: '大于0.15%',
              value: 1
            },
            {
              label: '小于0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.hnr>'0.15%';
            } else if (value === 2) {
              return row.hnr < '0.5%';
            }
          }
        },
        {
          title: 'time',
          key: 'time',
          sortable: true,
        }],
      totalSize:0,                      //数据的总条数
      pageSizeOpts:[10,30,50,100],      //可以选择的每页显示的条数
      placement:"top",                  //条数切换弹窗的展开方向
      changePageSize:10,                 //当前选择的是几 条/页
      List: [
        {
          value: 'default',
          label: '默认排序'
        },
        {
          value: 'jitterAsc',
          label: 'Jitter升序'
        },
        {
          value: 'jitterDes',
          label: 'Jitter降序'
        },
        {
          value: 'shimmerAsc',
          label: 'Shimmer升序'
        },
        {
          value: 'shimmerDes',
          label: 'Shimmer降序'
        },
        {
          value: 'hnrAsc',
          label: 'Hnr升序'
        },
        {
          value: 'hnrDes',
          label: 'Hnr降序'
        }
      ],
      model2: '',
      sortData:[{                //排序
        sort_Directions: '',
        sort_Properties: '',
      }],
      index:1
    }
  },
    created(){
      this.getHistory()
    },
    mounted(){

  },
    methods:{
      getHistory(){
        getHistoryUrl().then((res) =>{             //默认查询
          this.history = res.result[0].content
          this.totalSize = res.result[0].totalPages * res.result[0].size
        })
      },
      changePage(PageSize){                        //当前选择的是几 条/页
        this.changePageSize = PageSize
      },
      changeTable(index){                         //分页查询
        this.index = index
        if(this.sortData[0].sort_Directions == '' || this.sortData[0].sort_Properties == ''){
          getHistoryUrl(index,this.changePageSize).then((res) =>{
            if(res.status == '0'){
              this.history = res.result[0].content
              this.totalSize = res.result[0].totalPages * res.result[0].size
            }else{
             console.log(res.msg)
            }
          })
        }else{
          getSortUrl(index,this.changePageSize,this.sortData[0].sort_Directions,this.sortData[0].sort_Properties).then((res) =>{
            if(res.status == '0') {
              this.history = res.result[0].content
              this.totalSize = res.result[0].totalPages * res.result[0].size
            }else {
              console.log(res.msg)
            }
          })
        }
      },
      sort(value){
        if(value == 'jitterAsc'){
          this.sortData[0].sort_Directions = 'ASC',
            this.sortData[0].sort_Properties = 'jitter'
        }if(value == 'jitterDes'){
          this.sortData[0].sort_Directions = 'DESC',
            this.sortData[0].sort_Properties = 'jitter'
        }
        if(value == 'shimmerAsc'){
          this.sortData[0].sort_Directions = 'ASC',
            this.sortData[0].sort_Properties = 'shimmer'
        }if(value == 'shimmerDes'){
          this.sortData[0].sort_Directions = 'DESC',
            this.sortData[0].sort_Properties = 'shimmer'
        }
        if(value == 'hnrAsc'){
          this.sortData[0].sort_Directions = 'ASC',
            this.sortData[0].sort_Properties = 'hnr'
        }if(value == 'hnrDes'){
          this.sortData[0].sort_Directions = 'DESC',
            this.sortData[0].sort_Properties = 'hnr'
        }if(value == 'default'){
          this.sortData[0].sort_Directions = '',
            this.sortData[0].sort_Properties = ''
          this.index = 1
          getHistoryUrl(1,this.changePageSize).then((res) =>{
            if(res.status == '0'){
            this.history = res.result[0].content
            this.totalSize = res.result[0].totalPages * res.result[0].size
            }else {
              console.log(res.msg)
            }
          })
        }
        else{
          getSortUrl(this.index,this.changePageSize,this.sortData[0].sort_Directions,this.sortData[0].sort_Properties).then((res) =>{
            if(res.status == '0'){
            this.history = res.result[0].content
            this.totalSize = res.result[0].totalPages * res.result[0].size
            }else {
              console.log(res.msg)
            }
          })
          return
        }
      }
    },
    watch:{
      //监听changePageSize变化加载数据
      changePageSize(){
        this.changeTable(1)
      }
    },
  components:{
    BaseTable
  }
  }
</script>
<style lang="scss">
  .hietory-table{
   position: absolute;
    top:100px;
    left: 220px;
    right: 20px;
    z-index: 50;
  }
  .ivu-select-selected-value{
    width: 90px;
  }
</style>



